import React, { useEffect, useState  } from 'react'
import { connect } from 'react-redux'


import { getTimetables }  from './reducers/timetableReducer'
import Departures     from './components/Departures'
import ChooseBus from './components/ChooseBus'
import Menu from './components/Menu'
import { getLocalStoragePatterns } from './utils/LocalStorage'
import { setPatterns } from './reducers/followedPatternsReducer'
import { Container } from 'semantic-ui-react'
import FollowedPatternList from './components/FollowedPatternList'

const App = ({ setPatterns }) => {

  const [activeItem,setActiveItem] = useState('Departures')


  useEffect(() => {
    // Import from local storage
    setPatterns(getLocalStoragePatterns().filter(s => s.patternId))  

  },[])


  const pages = [
    { name: 'Aikataulut', componentName: 'Departures' }
    ,
    { name: 'Seurattavat bussit', componentName: 'FollowedPatternList' }
    ,
    { name: 'Valitse dösä', componentName: 'ChooseBus' }
  ]

  return(

    <Container>
      <Menu activeItem={activeItem} pages={pages} onChoose={setActiveItem}/>
       {
          
        activeItem === 'ChooseBus' ? 

            <ChooseBus />

            :
          
            activeItem === 'Departures' ? 

              <Departures/>
              :
              <FollowedPatternList/>
      
        } 
    </Container>

  )

}




const mapStateToProps = (state) =>  ({ ...state })
export default connect(mapStateToProps, {   getTimetables, setPatterns })(App)