import React from 'react'

import { Menu as SemanticMenu } from 'semantic-ui-react'


import uuidv4 from 'uuid/v4'

const Menu = ({ activeItem, pages, onChoose }) => {



    return(
        <SemanticMenu attached='top' tabular>
            {
                pages.map(page => {
                    return(
                        <SemanticMenu.Item
                        key={uuidv4()}
                        active={activeItem === page.componentName}
                        onClick={ () => onChoose(page.componentName) }
                        >
                            { page.name }
                        </SemanticMenu.Item>
                    )
                })
            }
        </SemanticMenu>
    )
}


export default Menu