
import React from 'react'
import { connect } from 'react-redux'


import { List, Button } from 'semantic-ui-react'
import { removePattern } from '../reducers/followedPatternsReducer'

import  uuidv4  from 'uuid/v4'

const FollowedPatternList = ({  followedPatterns, removePattern }) => {

    console.log('followedPatterns',followedPatterns)

    const makeListItem = pattern => {

        return(
            <List.Item key= { uuidv4() }>
                    <List.Content  floated='right'>
                        <Button negative onClick={ () => removePattern(pattern) }>Poista</Button>
                    </List.Content>
    
                    <List.Content style={{fontSize: '150%'}}> { pattern.shortName }</List.Content>
            </List.Item>
        )
    }

    return(
        <>
            <List divided verticalAlign='middle'>
                {
                    followedPatterns.map(makeListItem)
                }
            </List>
        </>
    )


}




const mapStateToProps = (state) =>  ({ ...state })
export default connect(mapStateToProps, {  removePattern  })(FollowedPatternList)
