import React, { useState, useEffect } from 'react'
import _ from 'lodash'
import searchStops from '../services/stops'
import { Search as SemanticSearch, Container } from 'semantic-ui-react'

const Search = ({ setSelectedStop, placeholder }) => {

    const [results,setResults] = useState([])
    const [value,setValue] = useState('')

    

    const handleSearchChange = (e, { value }) => {
        

        setValue(value)

        searchStops(value).then(res => res.json()).then(res => {
            console.log('res',res)
            setResults(res.data.stops.map(s => extractInfoForDisplay(s)  ))
        })
 
    }

    const handleResultSelect = (e, { result }) => {
         setSelectedStop(result)
         console.log('selected', result)
    }



    return(
        
        <Container>
            <SemanticSearch
                minCharacters={4}
                noResultsMessage='Pysäkkejä ei löydy'
                onResultSelect={ handleResultSelect}
                onSearchChange={ _.debounce( handleSearchChange , 500, {
                    leading: true,
                })}
                results={results}
                value={value}
                size='big'
                placeholder={placeholder}
                
            />
        </Container>   
        
    )
}


var extractInfoForDisplay = stop => {

    const bussesAndDirections = stop.patterns.map(pat =>  `(${ pat.headsign } ${pat.route.shortName})` )

    return {
        title: stop.name + ' ' + bussesAndDirections
        ,key: stop.gtfsId
        ,name: stop.name
        ,patterns: stop.patterns
    }

}


export default Search