const localStorageId = 'bustop-followed-patterns'

export const getLocalStoragePatterns = () => {
    const storageContent = localStorage.getItem(localStorageId)

    const patterns = storageContent === undefined | storageContent === null ? [] : storageContent.split(',')

    return patterns.map(pattern => ( { stopId: pattern.split('|')[0], patternId: pattern.split('|')[1], shortName: pattern.split('|')[2]    } ))

}


export const includedLocalStoragePatterns = patternid => {
    const storageContent = localStorage.getItem(localStorageId)

    return storageContent === undefined | storageContent === null ? false : ( storageContent.split(',').find(c => c === patternid) !== undefined )

}

export const addToLocalStoragePatterns = patternid => {
    const storageContent = localStorage.getItem(localStorageId)
    if ( storageContent === undefined | storageContent === null ) {
        localStorage.setItem(localStorageId,patternid)
    } else {
        localStorage.setItem(localStorageId,  storageContent.split(',').concat(patternid))
    } 
}


export const removeFromLocalStoragePatterns = patternid => {
    const storageContent = localStorage.getItem(localStorageId)
    if ( storageContent === undefined | storageContent === null ) {
        return undefined
    } else {
        localStorage.setItem(localStorageId,  storageContent.split(',').filter( c => c !== patternid  )  )
    } 
}

export const toggleLocalStoragePatterns = patternid => {

    if ( includedLocalStoragePatterns(patternid) ) {
        removeFromLocalStoragePatterns(patternid)
    } else {
        addToLocalStoragePatterns(patternid)
    }

}


