import { addToLocalStoragePatterns, removeFromLocalStoragePatterns } from '../utils/LocalStorage'

const initialstate = []



export const followedPatternsReducer = (state = initialstate, action) => {

  switch (action.type) {
  case 'ADD_PATTERN':
    return state.concat(action.data)
  case 'SET_PATTERNS':
    return action.data


  case 'REMOVE_PATTERN':
    return state.filter( pat => !(pat.stopId === action.data.stopId & pat.patternId === action.data.patternId  ))

  case 'INIT':
    return initialstate
    
  
  default:
    return state
  }
}

export const setPatterns = patterns => {

  return {
    type: 'SET_PATTERNS',
    data: patterns
  }

}


export const addPattern = pattern => {

  const patternId = idFromPattern(pattern)

  // Handle local storage
  addToLocalStoragePatterns( patternToString(pattern)  )

  return {
    type: 'ADD_PATTERN',
    data: patternId
  }
}



export const removePattern = pattern => {

  //const patternId = idFromPattern(pattern)

  console.log('remove', pattern)

  // Handle local storage
  removeFromLocalStoragePatterns( idPatternToString(pattern)  )

  return {
    type: 'REMOVE_PATTERN',
    data: pattern
  }
}


export const addPatterns = patterns => {


  // Handle local storage
  patterns.forEach(pattern =>  addToLocalStoragePatterns(  patternToString(pattern) ) )

  return {
    type: 'SET_PATTERNS',
    data: patterns.map(idFromPattern)
  }
}





const idFromPattern = pattern =>  ({ stopId: pattern.stop.key, patternId: pattern.pattern.code, shortName: pattern.pattern.route.shortName })
const patternToString = pattern => pattern.stop.key + '|' + pattern.pattern.code + '|' + pattern.pattern.route.shortName

// For situations where id from is already
const idPatternToString = pattern => pattern.stopId + '|' + pattern.patternId + '|' + pattern.shortName