import { getTimetables as getTimetablesService } from '../services/timetables'


const initialstate = null




export const timetableReducer = (state = initialstate, action) => {

  switch (action.type) {
  case 'SET_TIMETABLES':
    return action.data

  case 'INIT':
    return initialstate
  
  default:
    return state
  }
}

export const initData = () => {
  return {
    type: 'INIT',
    data: undefined
  }
}


export const getTimetables = (params) => {

   

    
    return async dispatch => {
      
      const data =      await getTimetablesService(params)  

      const parsedStream = await data.json()
      
      console.log('parsedStream',parsedStream)
      const manipulatedData = parsedStream.data.stops.flatMap(stop =>  stop.stoptimesWithoutPatterns.map(departure =>   ({ stopname: stop.name, stopId: stop.gtfsId, ...departure })        ) )
      
      manipulatedData.sort((tripA,tripB) => tripA.realtimeArrival - tripB.realtimeArrival)
      //console.log('resp', manipulatedData)
  
      return dispatch({
        type: 'SET_TIMETABLES',
        data: manipulatedData
      })
     
    }
}