import React, { useState } from 'react'
import Search from './Search'
import { connect } from 'react-redux'

import { List, Button,  Icon, Header, Grid } from 'semantic-ui-react'
import uuid from 'uuid'
import {  addPattern } from '../reducers/followedPatternsReducer'


const ChooseBus = ({  addPattern, followedPatterns }) => {

    const [selectedStop,setSelectedStop] = useState(undefined)
    


        
    const patternToListItem = pattern =>     {

        const icon = pattern.route.mode === 'RAIL' ? 'train' : pattern.route.mode.toLowerCase()

        // Dont add if already followed
        if (
            followedPatterns.find(p => p.patternId === pattern.code)
            ) {
            return('')
        }

        return(
            <List.Item key={ uuid() }>
                <List.Content floated='right'>
                    <Button
                        positive
                        onClick={ () => addPattern( { stop: selectedStop, pattern })  }
                    >
                        <Icon name={icon} style={{ fontSize: '150%', color: 'white' }}></Icon>
                        Seuraa
                    </Button>
                </List.Content>
                <List.Content>
                    <span style={{ marginRight: '2%' }}>Suunta: { pattern.headsign }</span>
                    <span style={{ fontSize: '150%'}}>{pattern.route.shortName}</span>
                    
                </List.Content>
            </List.Item>
        )
    }

    console.log('selectedStop', selectedStop)


    return(
        <Grid >
            <Grid.Row columns={3} style={{marginTop: '5%'}}>
                <Grid.Column>
                </Grid.Column>
                    <Grid.Column>
                        {
                            selectedStop ?
                                    <>
                                        <Header as='h2'>{ selectedStop.name }</Header>

                                        <List divided verticalAlign='middle'>
                                                {
                                                    selectedStop.patterns.map(patternToListItem)
                                                }
                                        </List>
                                        
                                    </>                                
                                :
                                    <Search setSelectedStop={setSelectedStop} placeholder="Pysäkin nimi" />
                            
                        }
                </Grid.Column>
                <Grid.Column>
                </Grid.Column>
            </Grid.Row>
        </Grid>
    )
}



const mapStateToProps = (state) =>  ({ ...state })
export default connect(mapStateToProps, {    addPattern })(ChooseBus)