const unixTimeToHoursAndMinutes = (t) => {

    // t = seconds from midnight

    let fullMinutes = (t - (t % 60)) / 60
    let hours =  (fullMinutes - (fullMinutes % 60)) / 60
    fullMinutes = ((fullMinutes % 60) )
    

    if (hours<10) {
        hours = '0'+hours
    }
    if (fullMinutes<10) {
        fullMinutes = '0'+fullMinutes
    }

    return hours +":"+fullMinutes
}

export default  unixTimeToHoursAndMinutes