
import React, { useEffect } from 'react'
import { connect } from 'react-redux'

import { Container, Card, Icon } from 'semantic-ui-react'

import  uuidv4  from 'uuid/v4'
import unixTimeToHoursAndMinutes from '../utils/UnixTime'
import { getTimetables } from '../reducers/timetableReducer'


const Departures = ({ followedPatterns, getTimetables, timetables }) => {

  
    useEffect(() => {

        if (followedPatterns && followedPatterns.length>0 ) {
            getTimetables( followedPatterns )
        }
            

    },[followedPatterns])

    if (!followedPatterns || followedPatterns.length===0) {

        return(
            <div>
                Ei seurattuja busseja.
            </div>
        )

    }

    
    
    if (!timetables) {
        return('')
    }
    const isDepartureFollowed = departure =>       followedPatterns.find(followed => followed.patternId === departure.trip.pattern.code)
    

    return(
        <Container style={ { marginTop:'5%' } }>

            <Card.Group>
            {
                timetables.filter(isDepartureFollowed).map(departure => {
                    let header = departure.trip.route.shortName

                    let realTime = unixTimeToHoursAndMinutes( departure.realtimeArrival )
                    let scheduled = unixTimeToHoursAndMinutes( departure.scheduledArrival) 
                    
                    const headerStyle = { fontSize: '120%' }


                    return(
                        <Card
                            key = { uuidv4() }
                            fluid
                        >
                            <Card.Content style={headerStyle}>
                                <span style={{color: '#006600', fontSize: '150%'}}>{ header } </span>
                                <Icon name="beer" style={{float: 'right', fontSize: '150%', color: '#eaea4e' }}></Icon>
                            </Card.Content>
                            <Card.Content>
                            <span><b> { realTime } </b></span>
                            <span style={{marginLeft:'5%'}}>{ scheduled }</span>
                            
                            </Card.Content>
                            
                            
                        </Card>
                    )

                })
            }
            </Card.Group>
        </Container>
    )
}

const mapStateToProps = (state) =>  ({ ...state })
export default connect(mapStateToProps, {   getTimetables })(Departures)