


export const getTimetables = async (params) => {

    console.log('params', params)

    const uniqueStopIds = params.reduce((t,c) => t.includes(c.stopId) ? t : t.concat(c.stopId)  , [])


    const stops = ['["', uniqueStopIds.join('","'),'"]'].join('')
   
    // Form query
    let query = '{ stops(ids: '+stops+')'
    
    

    query += '{   name gtfsId  stoptimesWithoutPatterns(numberOfDepartures:30) {'

    let paramsForApi = [
    'scheduledArrival',
    'realtimeArrival',
    'arrivalDelay',
    'scheduledDeparture',
    'realtimeDeparture',
    'departureDelay',
    'realtime',
    'realtimeState',
    'serviceDay',
    'headsign']

    query += paramsForApi.join(' ');
    // TODO: terrible
    query += ' trip {     gtfsId        tripShortName        serviceId  pattern { code }      route {          id          shortName          gtfsId        }              }'
    query += '   }       }      }';
    

    // Post it

    const timetables = await fetch('https://api.digitransit.fi/routing/v1/routers/hsl/index/graphql', {
            body: JSON.stringify({ query }),
            headers: {
            'user-agent': 'Mozilla/4.0 MDN Example',
            'content-type': 'application/json'
            },
            method: 'POST', 
            mode: 'cors', 
            redirect: 'follow', 
            referrer: 'no-referrer', 
        })

    return timetables//.then(res => res.json)


} 



