


const searchStops = async (searchString) => {

    
    //const searchString = 'hertto'//params.searchString
   
    // Form query
    //let query = '{ stops(ids: '+params.searchString+')'
    
    

    //query += '{   name gtfsId  stoptimesWithoutPatterns(numberOfDepartures:10) {'

   

    const query = `{
      stops(name: "${searchString}") {
        gtfsId
        name
        code
        lat
        lon
        patterns {
          code
          directionId
          headsign
          route {
            gtfsId
            shortName
            longName
            mode
          }
        }
      }
    }
    `
    // Post it

    const stops = await fetch('https://api.digitransit.fi/routing/v1/routers/hsl/index/graphql', {
            body: JSON.stringify({ query }),
            headers: {
            'user-agent': 'Mozilla/4.0 MDN Example',
            'content-type': 'application/json'
            },
            method: 'POST', 
            mode: 'cors', 
            redirect: 'follow', 
            referrer: 'no-referrer', 
        })

    return stops//.then(res => res.json)


} 



export default searchStops 